export const getAll = () => {
  return portfolio;
};

export const getById = id => {
  const portfolioItem = portfolio.find(portfolioItem => portfolioItem.id === id);
  if (!portfolioItem) {
    return null;
  }
  return Object.assign({}, portfolioItem);
};

export default { portfolio, getAll, getById };



